//    toJSON .
// eslint-disable-next-line
Date.prototype.toJSON = function () {
  var timezoneOffsetInHours = -(this.getTimezoneOffset() / 60); //UTC minus local time
  var sign = timezoneOffsetInHours >= 0 ? "+" : "-";
  var leadingZero = Math.abs(timezoneOffsetInHours) < 10 ? "0" : "";
  var correctedDate = new Date(
    this.getFullYear(),
    this.getMonth(),
    this.getDate(),
    this.getHours(),
    this.getMinutes(),
    this.getSeconds(),
    this.getMilliseconds()
  );
  correctedDate.setHours(this.getHours() + timezoneOffsetInHours);
  var iso = correctedDate.toISOString().replace("Z", "");
  return iso + sign + leadingZero + Math.abs(timezoneOffsetInHours).toString() + ":00";
};

export const prettyDate = (dateString) => {
  const str = dateString;
  const list = str.split(".")[0].split("T");
  const YMD = list[0].split("-"); //[0][1][2]
  const HMS = list[1].split(":"); //[0][1][2]
  const day = ["", "", "", "", "", "", ""][new Date(str).getDay()];

  const today = new Date().toJSON();
  const list_today = today.split(".")[0].split("T");
  const YMD_today = list_today[0].split("-"); //[0][1][2]
  // const HMS_today = list_today[1].split(":"); //[0][1][2]

  if (YMD[0] !== YMD_today[0] || YMD[1] !== YMD_today[1] || Number(YMD[2]) < Number(YMD_today[2]))
    return `${Number(YMD[0])}${Number(YMD[1])}${Number(YMD[2])}`;
  else if (YMD[2] === YMD_today[2]) return ` ${Number(HMS[0])}${Number(HMS[1])}`;
  else if (Number(YMD[2]) === Number(YMD_today[2]) + 1) return ` ${Number(HMS[0])}${Number(HMS[1])}`;
  else if (Number(YMD[2]) === Number(YMD_today[2]) + 2)
    return ` ${Number(YMD[2])} ${Number(HMS[0])}${Number(HMS[1])}`;
  else return ` ${day} ${Number(HMS[0])}${Number(HMS[1])}`;
};
